import React, { Component } from 'react';
import { Card, Button, ButtonGroup, CardTitle, Row, Col } from 'reactstrap';

class Home extends Component {

  render() {

    let days = [
      6,
      7,
      8,
      9,
      10,
      11,
      12
    ];

    let content = [];

    days.forEach((day, index) => {
      content.push(
        <Col key={index} className="my-2">
          <Card body>
            <CardTitle>Day {day}</CardTitle>
            <ButtonGroup>
              <Button onClick={() => this.props.history.push(`${process.env.PUBLIC_URL}/puzzle?id=Day${day}Part1`)}>Part 1</Button>
              <Button onClick={() => this.props.history.push(`${process.env.PUBLIC_URL}/puzzle?id=Day${day}Part2`)}>Part 2</Button>
            </ButtonGroup>
          </Card>
        </Col>
      );
    })

    return (
      <div className="Home">
        <Row>
          {content}
        </Row>
      </div>
    );
  }
}

export default Home;
